@import '../../../../utils/variables';

.actionMesgContainer {
  background: $lightGrey;
  min-height: 50vh;
  display: flex;
  padding: 18px;
  flex-direction: column;
}
.downArrow {
  font-size: 5em;
  color: $grey;
  align-self: center;
  animation: pulseDown 1s infinite;
  right: 50%;
  bottom: 0;
}
@keyframes pulseDown {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-4px);
  }
}

.actionMesg {
  color: $blue;
  text-align: center;
  margin: auto;
  max-width: 800px;
}

// how it works
.processContainer {
  display: flex;
  flex-direction: column;
  color: $blue;
  margin: 4em 0;
  & h1 {
    text-align: center;
  }
  & .stepContainer:nth-of-type(2) {
    background: $lightGrey;
    & .stepContent {
      flex-wrap: wrap;
    }
  }
}
.stepContainer {
  padding: 0 18px;
}
.stepContent {
  max-width: 1100px;
  display: flex;
  flex-wrap: wrap-reverse;
  align-items: center;
  margin: 4em auto;
  width: 100%;
  justify-content: space-between;
}
.stepDetails {
  flex: 1 50%;
  max-width: 400px;
  position: relative;
}
.stepBackground {
  position: absolute;
  width: 50%;
  left: 0;
  right: 0;
  margin: auto;
}
.stepImg {
  width: 50%;
  display: flex;
  align-items: center;
  min-width: 300px;
  max-width: 500px;
  margin-bottom: 4em;
  & img {
    max-width: 100%;
  }
}

.cta {
  text-align: center;
  margin: auto;
  padding: 0 18px;

  & h4 {
    color: $blue;
    width: 100%;
  }
}
